@import url('https://fonts.googleapis.com/css2?family=Dela+Gothic+One&family=Encode+Sans+Condensed:wght@200;300;400;700&family=Galada&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Denk+One&display=swap');

@media (min-width: 600px) {

    .instagram-section {
        background-image: url(../img/instagram-background.webp);
        background-size: cover;
        background-repeat: no-repeat;
        background-position: 0 0px;
        padding: 32px 0px;
    }

    .instagram-section-phrase>p {
        font-family: 'Galada', cursive;
        font-weight: bold;
        font-size: 3em;
        color: #000;
        padding-bottom: 32px;
        margin: 0;
    }

    .instagram-section-hashtag {
        font-family: 'Denk One', sans-serif;
        color: #000;
        
    }

    .instagram-section-image>img {
        height: auto;
        max-width: 15vw;
    }

    .instagram-section-hashtag>p {
        font-size: 4em;
        margin: 0;
    }

    .instagram-section-button {
        background-color: #2A7168;
        border-radius: 50px;
        color: black;
        padding: 0.2em 0em;
        text-align: center;
        text-decoration: none;
        font-size: 2em;
        min-width: 30vw;
        font-family: 'Encode Sans Condensed', sans-serif;
        font-weight: bold;
        border-style: none;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    }

    .instagram-section-button>a>p {
        margin: 0;
    }

    .instagram-link:link,
    .instagram-link:visited {
        color: #fff;
        text-decoration: none;
    }

}

@media (max-width: 600px) {

    .instagram-section {
        background-image: url(../img/instagram-background.webp);
        background-size: cover;
        background-repeat: no-repeat;
        padding: 32px 0px;
     }

    .instagram-section-phrase>p {
        font-family: 'Galada', cursive;
        font-size: 1.3em;
        margin: 0px;
        padding-bottom: 50px;
        color: #000;
    }

    .instagram-section-image>img {
        height: auto;
        max-width: 20vw;
    }

    .instagram-section-hashtag {
        font-family: 'Denk One', sans-serif;
        padding-top: 16px;
    }

    .instagram-section-hashtag>p {
        font-size: 3em;
        margin: 0;
        padding-bottom: 20px;
        color: #000;
    }

    .instagram-section-button {
        background-color: #2A7168;
        border-radius: 50px;
        color: black;
        padding: 0.5em 0.4em;
        text-align: center;
        text-decoration: none;
        display: inline-block;
        font-size: 1em;
        min-width: 50vw;
        font-family: 'Encode Sans Condensed', sans-serif;
        font-weight: bold;
        border-style: none;
        box-shadow: 0px 4px 4px rgba(0, 0, 0, 0.25);
    }

    .instagram-section-button>a>p {
        margin: 0;
    }

    .instagram-link:link,
    .instagram-link:visited {
        color: #fff;
        text-decoration: none;
    }


}